@font-face {
  font-family: "Avenir Next Cyr";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Avenir Next Cyr"), url("../fonts/AvenirNextCyr-Regular.woff") format(woff);
}
@font-face {
  font-family: "Avenir Next Cyr";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Avenir Next Cyr"), url("../fonts/AvenirNextCyr-Medium.woff") format(woff);
}
@font-face {
  font-family: "Avenir Next Cyr";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Avenir Next Cyr"), url("../fonts/AvenirNextCyr-Bold.woff") format(woff);
}
body {
  margin: 0;
  padding: 0;
  padding-bottom: 200px;
  font-family: "Avenir Next Cyr", "Arial", sans-serif;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.5);
  background: linear-gradient(0deg, #20afff 0%, #20afff 46%, #12033c 100%);
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: #fff;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
img {
  max-width: 100%;
}
.container {
  max-width: 1200px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}
@media (max-width: 1440px) {
  .container {
    max-width: 1000px;
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (max-width: 768px) {
  .container {
    min-width: 280px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.slider {
  padding-top: 115px;
  background-color: #20AFFF;
}
@media (max-width: 768px) {
  .slider {
    padding-top: 45px;
  }
}
.slider__swiper {
  height: auto;
  min-width: 280px;
}
.slider__slide {
  box-sizing: border-box;
  padding: 2% 16.6% 3.6% 13.2%;
  margin-bottom: 73px;
  height: auto;
  color: #004460;
  background-color: #fff;
  border-radius: 35px;
}
@media (max-width: 768px) {
  .slider__slide {
    padding: 40px 60px;
