}
.slider__description {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 90%;
  font-size: 18px;
  line-height: 26px;
}
@media (max-width: 1440px) {
  .slider__description {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (max-width: 768px) {
  .slider__description {
    width: 100%;
    padding-bottom: 30px;
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 22px;
    border-bottom: 1px solid #B9B9B9;
