}
.swiper-pagination-bullet {
  width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 13px));
  height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 13px));
}
@media (max-width: 1440px) {
  .swiper-pagination-bullet {
    width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 10px));
    height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 10px));
  }
}
.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 11px);
}
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 0;
}
.swiper-pagination-bullet {
  box-sizing: border-box;
  background-color: transparent;
  border: 1px solid #fff;
  opacity: 0.5;
}
.swiper-pagination-bullet-active {
  background-color: #fff;
  opacity: 1;
}
.swiper-button-prev {
  left: 12%;
  top: 45%;
}
@media (max-width: 768px) {
  .swiper-button-prev {
    display: none;
  }
}
.swiper-button-next {
  right: 12%;
  top: 45%;
}
@media (max-width: 768px) {
  .swiper-button-next {
    display: none;
  }
}
.swiper-button-prev:after,
.swiper-button-next:after {
  font-size: 4vw;
}
.button {
  box-sizing: border-box;
  font-family: "Avenir Next Cyr", "Arial", sans-serif;
  display: inline-block;
  padding: 15px 30px;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  color: #004460;
  background-color: #fff;
  border-radius: 3px;
  border: none;
}
@media (max-width: 1440px) {
  .button {
    font-size: 13px;
  }
}
@media (max-width: 768px) {
  .button {
    width: 100%;
  }
}
.language {
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 0;
  gap: 19px;
  text-transform: uppercase;
}
.language__wrapper {
  position: relative;
}
.language__wrapper_gray {
  color: #B9B9B9;
}
.language__wrapper:not(:last-child):after {
  position: absolute;
  content: "/";
  top: 0;
  right: -12px;
  color: inherit;
}
.language__item {
  position: relative;
  cursor: pointer;
}
.language__item:hover {
  font-weight: 500;
  color: #20AFFF;
}
.language__input:checked + .language__item {
  font-weight: 500;
  color: #20AFFF;
}
.header {
  position: sticky;
  top: 0;
  z-index: 50;
  font-weight: 500;
  background-color: #12033C;
}
@media (max-width: 768px) {
  .header {
    position: relative;
    display: flex;
  }
}
@media (max-width: 768px) {
  .header_with-paddings {
    padding: 27px 30px 0;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 768px) {
  .header__burger_visible {
    display: inline-block;
    position: absolute;
    top: 18px;
    right: 9px;
    width: 70px;
    height: 50px;
    cursor: pointer;
    background-color: transparent;
    border: none;
    outline: none;
  }
}
.header__burger-line {
  position: absolute;
  top: 25px;
  left: 26px;
  width: 24px;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease-out;
}
.header__burger-line_closed::after,
.header__burger-line_closed::before {
  content: "";
  position: absolute;
  bottom: 8px;
  left: 0;
  width: 24px;
  height: 2px;
  transition: all 0.3s ease-out;
  background-color: #fff;
}
.header__burger-line_closed::after {
  top: 8px;
}
.header__burger-line_opened {
  background-color: transparent;
}
.header__burger-line_opened::after,
.header__burger-line_opened::before {
  content: "";
  position: absolute;
  top: -1px;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: #004460;
  transform: rotate(45deg);
  transition: all 0.3s ease-out;
}
.header__burger-line_opened::after {
  transform: rotate(-45deg);
}
@media (max-width: 768px) {
  .navigation {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 100%;
